import React, { forwardRef, useCallback, useState } from 'react'

import { Movement, Point, findControlPoint } from '~/lib/Point'
import { shouldBePresent } from '~/lib/asserters/commonAsserters'
import classes from './classes.css'

interface InnerProps {
  width: number
  height: number
  innerRef: React.Ref<HTMLCanvasElement>
  context: CanvasRenderingContext2D | null | undefined
}

type Props = React.PropsWithRef<Alt.Omit<InnerProps, 'innerRef'>>

const Canvas: React.FunctionComponent<InnerProps> = ({ width, height, innerRef, context }) => {
  const [drawing, setDrawing] = useState(false)
  const [startPoint, setStartPoint] = useState<Point | null>(null)
  const [startMovement, setStartMovement] = useState<Movement | null>(null)

  const handlePointerDown = useCallback<React.PointerEventHandler<HTMLCanvasElement>>((event) => {
    setDrawing(true)

    setStartPoint({
      x: event.nativeEvent.offsetX,
      y: event.nativeEvent.offsetY,
    })

    setStartMovement({
      x: event.movementX,
      y: event.movementY,
    })
  }, [])

  const handlePointerMove = useCallback<React.PointerEventHandler<HTMLCanvasElement>>((event) => {
    if (!drawing) {
      return
    }

    shouldBePresent(context)
    shouldBePresent(startPoint)
    shouldBePresent(startMovement)

    context.lineCap = 'round'

    context.beginPath()
    context.moveTo(startPoint.x, startPoint.y)

    const endPoint: Point = {
      x: event.nativeEvent.offsetX,
      y: event.nativeEvent.offsetY,
    }

    const endMovement: Movement = {
      x: event.movementX,
      y: event.movementY,
    }

    const controlPoint = findControlPoint(startPoint, endPoint, startMovement, endMovement)

    context.quadraticCurveTo(controlPoint.x, controlPoint.y, endPoint.x, endPoint.y)

    context.stroke()

    setStartPoint(endPoint)
    setStartMovement(endMovement)
  }, [context, drawing, startPoint, startMovement])

  const handlePointerUp = useCallback(() => {
    setDrawing(false)
  }, [])

  const handlePointerLeave = useCallback(() => {
    setDrawing(false)
  }, [])

  return (
    <canvas
      ref={ innerRef }
      className={ classes.Canvas }
      width={ width }
      height={ height }
      onPointerDown={ handlePointerDown }
      onPointerMove={ handlePointerMove }
      onPointerUp={ handlePointerUp }
      onPointerLeave={ handlePointerLeave }
    />
  )
}

export default forwardRef<HTMLCanvasElement, Props>((props, ref) => (
  <Canvas innerRef={ ref } { ...props } />
))
