export interface Point {
  x: number
  y: number
}

export interface Movement {
  x: number
  y: number
}

export function findControlPoint(start: Point, end: Point, dstart: Movement, dend: Movement): Point {
  const intersectionPoint = findIntersectionPoint(start, end, dstart, dend)

  if (!Number.isFinite(intersectionPoint.x) || !Number.isFinite(intersectionPoint.y)) {
    return findMiddlePointBetween(start, end)
  }

  return intersectionPoint
}

function findMiddlePointBetween(start: Point, end: Point): Point {
  return {
    x: start.x + (end.x - start.x) / 2,
    y: start.y + (end.y - start.y) / 2,
  }
}

function findIntersectionPoint(start: Point, end: Point, dstart: Movement, dend: Movement): Point {
  const k = (dend.y * (end.x + dend.x - start.x) - dend.x * (end.y + dend.y - start.y)) / (dend.y * dstart.x - dend.x * dstart.y)

  // NOTE: Number.isNaN(k) → dend.y * dstart.x === dend.x * dstart.y
  const parallel = Number.isNaN(k)

  if (parallel) {
    return {
      x: dstart.x !== 0 ? start.x + Math.sign(dstart.x) * Infinity : start.x,
      y: dstart.y !== 0 ? start.y + Math.sign(dstart.y) * Infinity : start.y,
    }
  }

  // NOTE: !Number.isFinite(start) || !Number.isFinite(end) の場合、 !Number.isFinite(k) なので、無限遠点を返す。
  return {
    x: start.x + k * dstart.x,
    y: start.y + k * dstart.y,
  }
}
